import type { VariantModes, WeightByVariant } from './types';

import styles from './styles.scss';

/** Генерация className с параметрами начертания. При получении значения inherit класс добавлен не будет
 * @param {VariantModes} font - название начертания
 * @param {WeightByVariant} weight - жирность, применяемая для соответтсвующего начертания
 * @param {boolean} [isEmpty] - флаг, не генерировать классы
 * */
const useFont = <M extends VariantModes>(font: M, weight: WeightByVariant<M>, isEmpty = false): string | undefined => {
  if (isEmpty) return undefined;

  /** ВАЖНО: не добавлять в styles значение inherit - стили для наследования при необходимости
   * прописывать в самих компонентах */
  return [
    styles[`${ font || '' }`], // font variant/size
    styles[weight] // font-weight
  ].filter(Boolean).join(' ');
};
export default useFont;
